// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1320px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #000000;
  --text-color: rgb(131, 130, 130);
  --accent-color: #ffb600;
}
